<template>
  <div class="form-con">
    <h2 class="title">
      {{$route.name == '修改推广链接' ? '修改推广链接' : '创建推广链接'}}
    </h2>
    <Form :model="form" class="form-group" label-position="right" :label-width="100">
      <FormItem label="id" prop="id">
        <Input class="inputtxt" size="large" type="text" v-model.trim="form.id" placeholder="请输入id"></Input>
      </FormItem>
      <FormItem label="标题" prop="title">
        <Input class="inputtxt" size="large" type="text" v-model.trim="form.title" placeholder="请输入标题"></Input>
      </FormItem>
      <FormItem label="链接" prop="link">
        <Input class="inputtxt" size="large" type="text" v-model.trim="link" placeholder="请输入链接"></Input>
        <Button style="margin-left: 20px;" @click="addOne" type="success" size="large">添加</Button>
      </FormItem>
      <FormItem label="链接组" prop="links">
        <ul>
          <li v-for="(item, index) in form.links" :key="index">
            <span>{{item}}</span>
            <Button type="error" size="large" @click="deleteLink(index)">X</Button>
          </li>
        </ul>
      </FormItem>
      <FormItem label="返回链接" prop="back_url">
        <Input class="inputtxt" size="large" type="text" v-model.trim="back_url" placeholder="请输入返回链接"></Input>
        <Button style="margin-left: 20px;" @click="addBackUrl" type="success" size="large">添加</Button>
      </FormItem>
      <FormItem label="返回链接组" prop="back_urls">
        <ul>
          <li v-for="(item, index) in form.back_urls" :key="index">
            <span>{{item}}</span>
            <Button type="error" size="large" @click="deleteBackUrl(index)">X</Button>
          </li>
        </ul>
      </FormItem>
      <FormItem label="是否增加权重" prop="title">
        <i-switch v-model="type"/>
      </FormItem>
      <FormItem v-if="type" label="权重" prop="weight">
        <Input class="inputtxt" size="large" type="text" v-model.trim="weight" placeholder="请输入权重"></Input>
        <Button style="margin-left: 20px;" @click="addWeight" type="success" size="large">添加</Button>
      </FormItem>
      <FormItem v-if="type" label="权重组" prop="weights">
        <ul>
          <li v-for="(item, index) in form.weights" :key="index" class="weight">
            <div v-if="selectIndex === index" class="input-box">
              <Input v-model.trim="weight_input" search enter-button="保存" placeholder="请输入权重" @on-search="changeWeight(index)" />
            </div>
            <div v-else>
              <span @click="changeState(item, index)">{{item}}</span>
              <Button type="error" @click="deleteWeight(index)">DEL</Button>
            </div>
          </li>
        </ul>
      </FormItem>
      <FormItem v-if="type" label="投放状态" prop="status">
        <i-switch v-model="status"/>
      </FormItem>
      <FormItem v-if="type" label="时间颗粒度" prop="granularity">
        <InputNumber class="inputtxt" :min="1" v-model.trim="form.granularity"></InputNumber>
      </FormItem>
      <FormItem label="备注" prop="remarks">
        <Input class="inputtxt" size="large" type="text" v-model.trim="form.remarks" placeholder="请输入备注"></Input>
      </FormItem>
      <FormItem>
        <Button v-if="$route.name == '创建推广链接'" class="edit-btn" size="large" type="primary" @click="create">立即创建
        </Button>
        <Button v-else class="edit-btn" size="large" type="primary" @click="save">保 存</Button>
        <Button class="edit-btn" size="large" type="warning" @click="cancel">取 消</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          links: [],
          weights: [],
          back_urls: []
        },
        link: '',
        weight: '',
        type: false,
        selectIndex: -1,
        weight_input: '',
        status: false,
        back_url: ''
      }
    },
    mounted() {
      if (this.$route.name === '修改推广链接') {
        this.form = this.$route.params.row;
        this.type = this.form.type == 1 ? true : false;
        this.status = this.form.status == 1 ? true : false;
        if (!this.form.links) {
          this.form.links = []
        }
        if(!this.form.weights) {
          this.form.weights = []
        }
      }
    },
    methods: {
      changeState(item, index) {
        this.selectIndex = index;
        this.weight_input = item;
      },
      changeWeight(index) {
        this.form.weights.splice(index, 1, this.weight_input);
        this.selectIndex = -1;
      },
      deleteLink(index) {
        this.$Modal.confirm({
          title: '提示:',
          content: '确认删除这条内容吗？',
          onOk: () => {
            this.form.links.splice(index, 1);
          }
        })
      },
      addOne() {
        if (this.link === "") {
          this.$Message.info("链接不能为空");
        } else {
          this.form.links.push(this.link);
          this.link = "";
        }
      },
      deleteWeight(index) {
        this.$Modal.confirm({
          title: '提示:',
          content: '确认删除这条内容吗？',
          onOk: () => {
            this.form.weights.splice(index, 1);
          }
        })
      },
      addWeight() {
        if (this.weight === "") {
          this.$Message.info("权重不能为空");
        } else {
          this.form.weights.push(this.weight);
          this.weight = "";
        }
      },
      addBackUrl() {
        if (this.back_url === "") {
          this.$Message.info("返回链接不能为空");
        } else {
          this.form.back_urls.push(this.back_url);
          this.back_url = "";
        }
      },
      deleteBackUrl(index) {
        this.$Modal.confirm({
          title: '提示:',
          content: '确认删除这条内容吗？',
          onOk: () => {
            this.form.back_urls.splice(index, 1);
          }
        })
      },
      save() {
        this.form.type = this.type ? 1 : 0;
        this.form.status = this.status ? 1 : 0;
        this.$axios.post('/transfer/update', this.form)
          .then(res => {
            if (res.data.success) {
              this.$Message.info(res.data.success);
              this.$router.push({
                name: '推广链接',
                params: {
                  row: this.form,
                  index: this.$route.params.index
                }
              });
              this.reset()
            } else {
              this.$Message.info(res.data.err);
            }
          });
      },
      create() {
        this.form.type = this.type ? 1 : 0;
        this.form.status = this.status ? 1 : 0;
        this.$axios.post('/transfer/create', this.form)
          .then(res => {
            if (res.data.success) {
              this.$Message.info(res.data.success);
              this.$router.push({
                name: '推广链接',
                params: res.data.data
              });
              this.reset()
            } else {
              this.$Message.info(res.data.err);
            }
          });
      },
      cancel() {
        this.$router.push({
          name: '推广链接',
          params: {state: 'cancel'}
        });
        this.reset()
      },
      reset() {
        this.form = {
          links: [],
          type: 0,
          weights: []
        };
      }
    }
  }
</script>
<style scoped>
  .form-con {
    width: 900px;
    padding-top: 20px;
    margin: 0 auto;
    text-align: left;
  }

  .title {
    font-size: 36px;
    text-align: center;
    margin-bottom: 30px;
  }

  .inputtxt {
    width: 400px;
  }

  ul {
    list-style: none;
    overflow: hidden;
  }

  li {
    float: left;
    margin: 0 20px 20px 0;
  }

  li span {
    display: inline-block;
    padding: 0 15px;
    border: 1px solid #eee;
    border-radius: 5px;
    margin-right: 10px;
    cursor: pointer;
    text-align: center;
  }
  .weight span {
    width: 100px;
  }

  .input-box {
    width: 150px;
    height: 34px;
  }

  .edit-btn {
    margin-right: 20px;
  }
</style>
